body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  min-height: 100vh;
  width: auto;
  background: whitesmoke;
  text-align: center;
}
.App__header {
  height: 100px;
  width: 100%;
  background: black;
  color: white;
}
.App__searchForm {
  height: 30px;
  width: 100%;
  background: lightslategray;
  padding-top: 10px;
}
.App__filterSection {
  height: 30px;
  width: 100%;
  background: lightgrey;
  padding-top: 10px;
  display: flex;
  justify-content: center;
}

.App__listItem {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  border-top: 1px solid darkgray;
}

.App__bookTitle {
  width: 100%;
}

.App__thumbnail {
  flex-basis: 1;
  padding-bottom: 10px;
  padding-top: 10px;
}

.App__bookInfo {
  flex-basis: 250px;
  padding-bottom: 10px;
}

